import React,{Component} from 'react';
import axios from 'axios';
import Restdisplay from './Restdisplay';
const durl="https://liveapis.herokuapp.com/restaurantdetails";
class Restdetails extends Component{
    constructor(){
        super()
        this.state={
            "details":''
        }
    }
    render(){
        return(
            <React.Fragment>
                <Restdisplay restdata={this.state.details}/>
            </React.Fragment>
        
        )
    }
    async componentDidMount(){
        var detailid=this.props.match.params.id
        let response=await axios.get(`${durl}/${detailid}`)
        this.setState({details:response.data[0]})
    }
    
        
}
export default Restdetails;