import React,{Component} from 'react';
import axios from 'axios';
import ListingDisplay from './listingdisplay';
import SuggestionBlock from './SuggestionLogic';
import Costfilter from '../filters/costfilter';
import Cuisinefilter from '../filters/cuisinefilter';
import SortFilter from '../filters/sortfilter';
const qurl="https://liveapis.herokuapp.com/restaurant?mealtype=";
const mealurl="https://liveapis.herokuapp.com/mealtype";
var limit = 3
class RestListing extends Component{
    constructor(props){
        super()
        this.state={
            hotellist:'',
            mealname:'',
            activePage: 1,
            totalNoOfItems: 1
        }
    }
    setDataAsPerFilter(sorteddata){
        this.setState({hotellist:sorteddata})
    }
    render(){
        return(
            <div className="container">
                <div className="row">
                    <div className="col-md-2">
                        <Costfilter costdata={(data)=>{this.setDataAsPerFilter(data)}}/>
                        <Cuisinefilter cuisinedata={(data)=>{this.setDataAsPerFilter(data)}}/>
                        <SortFilter sortdata={(data)=>{this.setDataAsPerFilter(data)}}/>
                    </div>
                    <div className="col-md-10">
                        <SuggestionBlock/>
                        <center>
                            <h3>Here are Options for {this.state.mealname}</h3>
                        </center>
                        <ListingDisplay listdata={this.state.hotellist} activePage={this.state.activePage} limit={limit} totalNoOfItems={this.state.totalNoOfItems} pageNumber={(data)=>{this.setState({activePage:data})}}
                        />
                    </div>
                </div>
            </div>
            
        )
    }
    componentDidMount(){
        var tripid=this.props.match.params.id
        sessionStorage.setItem('tripid',tripid)
        axios.get(`${mealurl}`)
        .then((response)=>{
            for(var i=0;i<response.data.length;i++){
                if(response.data[i]._id=== Number(tripid)){
                    this.setState({mealname:response.data[i].name})
                    i=response.data.length;
                }else{
                    this.setState({mealname:'Wrong Option'})
                }
            }
            
        })
        fetch(`${qurl}${tripid}`)
        .then((res)=>res.json())
        .then((data)=>{this.setState({
            hotellist:data.slice(0,data.length-1),
            totalNoOfItems:data.length-1
        })})
         
    }
}
export default RestListing;