import React,{Component} from 'react';
import {withRouter} from 'react-router-dom';
import './search.css';
const lurl="https://liveapis.herokuapp.com/location";
const rurl="https://liveapis.herokuapp.com/restaurant?city=";
class Search extends Component{
    constructor(){
        super()
        this.state={
            location:'',
            restaurents:''
        }
    }
    displayCity=(data)=>{
        if(data){
            return data.map((item)=>{
                return(
                    <option value={item.city}>
                         {item.city_name}
                    </option>
                )
            })
        }
    }
    displayrestaurent=(data)=>{
        if(data){
            return data.map((item)=>{
                return(
                    <option value={item._id}>
                         {item.name}|{item.locality}
                    </option>
                )
            })
        }
    }
    handlecity=(event)=>{
        const cityid=event.target.value
        const curl=`${rurl}${cityid}`
        fetch(curl,{Method:'GET'})
        .then((res)=>res.json())
        .then((data)=>{this.setState({restaurents:data})})
    }
    handleRestaurant=(event)=>{
        this.props.history.push(`/details/${event.target.value}`)
    }
    render(){
        console.log(this.props)
        console.log(this.state.location);
        return(
            <div className="imageContainer">
            <div id="logo">
                e!
            </div>
            <div className="heading">
                Find the best restaurants,cafes, bars
            </div>
            <div className="locationSelector">
                <select className="locationDropDown" onChange={this.handlecity}>
                    <option>Select city</option>
                    {this.displayCity(this.state.location)}
                </select>
                <select className="locationDropDown" onChange={this.handleRestaurant}>
                    <option>Select restaurent</option>
                    {this.displayrestaurent(this.state.restaurents)}
                </select>
            </div>
        </div>
        )
    }
    componentDidMount(){
        fetch(lurl,{Method:'GET'})
        .then((res)=>res.json())
        .then((data)=>{this.setState({location:data})})
    }
}
export default withRouter(Search);