import React from 'react';
import {Link} from 'react-router-dom';
import {Tab,Tabs,TabList,TabPanel} from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import './restdisplay.css';
const Restdisplay=(props)=>{
    const renderhotel=({restdata})=>{
        if(restdata){
            return(
                <div className="container">
                    <div className="panel panel-primary">
                        <div className="panel-heading">
                            <h3>{restdata.name}</h3>
                        </div>
                        <div className="panel-body">
                            <div className="row">
                                <div className="col-md-12">
                                    <button type="button" data-toggle="modal" data-target="#myModal">
                                        <img className="img-responsive" src={restdata.thumb}
                                            style={{width:1080,height:400}}/>
                                    </button>
                                    <div id="myModal" className="modal fade" role="dialog">
                                        <div class="modal-dialog">
                                            <div className="modal-content">
                                                <div className="modal-header">
                                                    <button type="button" className="close" data-dismiss="modal">&times;</button>
                                                    <h4 className="modal-title">Gallery of  {restdata.name}</h4>
                                                </div>
                                                <div className="modal-body">
                                                <div id="myCarousel" class="carousel slide" data-ride="carousel">
                                                    
                                                    <ol class="carousel-indicators">
                                                        <li data-target="#myCarousel" data-slide-to="0" class="active"></li>
                                                        <li data-target="#myCarousel" data-slide-to="1"></li>
                                                        <li data-target="#myCarousel" data-slide-to="2"></li>
                                                    </ol>

                                                    
                                                    <div class="carousel-inner">
                                                        <div class="item active">
                                                            <img className="img-responsive" src={restdata.thumb}
                                                        style={{width:1080,height:400}}/>
                                                        </div>

                                                        <div class="item">
                                                        <img className="img-responsive" src="/images/Lunch.png"
                                                        style={{width:1080,height:400}}/>
                                                        </div>

                                                        <div class="item">
                                                            <img className="img-responsive" src="/images/snacks.png"
                                                        style={{width:1080,height:400}}/>
                                                        </div>
                                                    </div>

                                                    
                                                    <a class="left carousel-control" href="#myCarousel" data-slide="prev">
                                                        <span class="glyphicon glyphicon-chevron-left"></span>
                                                        <span class="sr-only">Previous</span>
                                                    </a>
                                                    <a class="right carousel-control" href="#myCarousel" data-slide="next">
                                                        <span class="glyphicon glyphicon-chevron-right"></span>
                                                        <span class="sr-only">Next</span>
                                                    </a>
                                                    </div>

                                                        
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-12" id="heading">
                                    {restdata.name}
                                </div>
                            </div>
                            <div className="row">
                                <Tabs>
                                    <TabList>
                                        <Tab>Overview</Tab>
                                        <Tab>Contact</Tab>
                                    </TabList>
                                    <TabPanel>
                                        <div className="container">
                                        <h2><b>About this place</b></h2>
                                        <h3><b>Cuisine</b></h3>
                                        <p>{restdata.Cuisine[0].name} , {restdata.Cuisine[1].name}</p>
                                        <h3><b>Cost</b></h3>
                                        <p>{restdata.cost}Rs/-</p>
                                        </div>   
                                    </TabPanel>
                                    <TabPanel>
                                        <div className="container">
                                            <p className="hotelname">
                                                <b>{restdata.name}</b><br/>{restdata.address}</p>
                                            <p><b>Phone Number:</b></p>
                                            <p className="number">{restdata.contact_number}</p>
                                        </div>
                                    </TabPanel>
                                </Tabs>
                            </div>
                            <div className="row">
                                <Link to={`/list/${sessionStorage.getItem('tripid')}`} className="btn btn-danger">Back</Link>&nbsp;
                                <Link to={`/booking/${restdata._id}`} className="btn btn-success">Place Order</Link>
                            </div>
                        </div>
                    </div>
                </div>
            ) 
        }else{
            return(
                <div>
                    <img src="/images/loader.gif"/>
                </div>
            )
        }
    }
    return(
        <React-Fragment>
            {renderhotel(props)}
        </React-Fragment>
    )
}
export default Restdisplay;