import React from 'react';
import {withRouter} from 'react-router-dom';
const viewBooking=(props)=>{
    console.log("function>>>",(props.location.search).split('=')[1])
    const renderTable=({orderdata})=>{
        if(orderdata){
            return orderdata.map((item)=>{
                return(
                    <tr>
                        <td>{item._id}</td>
                        <td>{item.rest_id}</td>
                        <td>{item.name}</td>
                        <td>{item.phone}</td>
                        <td>{item.address}</td>
                    </tr>
                )
            })
        }
    }
    return(
        <div className="container">
            <alert className="alert alert-success">
                {(props.location.search).split('=')[1]}
            </alert>
            <center><h3>Order details</h3></center>
            <table className="table table-responsive">
                <thead>
                    <tr>
                        <th>Order Id</th>
                        <th>Restaurant</th>
                        <th>Name</th>
                        <th>Phone</th>
                        <th>Address</th>
                    </tr>
                </thead>
                <tbody>
                    {renderTable(props)}
                </tbody>
            </table>
        </div>
    )
    
}
export default withRouter(viewBooking);