import React,{Component} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';
const resturl="https://liveapis.herokuapp.com/restaurantdetails";
const bookingurl="https://liveapis.herokuapp.com/placeorder";
class Placeorder extends Component{
    constructor(){
        super()
        this.state={
            order_id:Math.floor(Math.random()*1000),
            hotel_name:'',
            name:'',
            phone:'',
            person:'',
            address:''
        }
    }
    handleName=(event)=>{
        this.setState({name:event.target.value})
    }
    handlePhone=(event)=>{
        this.setState({phone:event.target.value})
    }
    handlePerson=(event)=>{
        this.setState({person:event.target.value})
    }
    handleAddress=(event)=>{
        this.setState({address:event.target.value})
    }
    handleSubmit=()=>{
        fetch(bookingurl,{
            method:'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json',
            },
            body:JSON.stringify(this.state)
        })
        .then((this.props.history.push('/viewBooking?message=Success')))
    }
    async componentDidMount(){
        let restid=this.props.match.params.id;
        let response=await axios.get(`${resturl}/${restid}`)
        this.setState({rest_name:response.data[0].name})
    }
    render(){
        return(
            <div className="container">
                <div className="panel panel-info">
                    <div className="panel-heading">
                        <h4>Place Order</h4>
                    </div>
                </div>
                <div className="panel-body">
                    <div className="form-group">
                        <label>Order_id</label>
                        <input type="text" name="order_id" value={this.state.order_id} readOnly className="form-control"/>
                    </div>
                    <div className="form-group">
                        <label>Restaurant</label>
                        <input type="text" name="rest_name" value={this.state.rest_name} readOnly className="form-control"/>
                    </div>
                    <div className="form-group">
                        <label>Name</label>
                        <input type="text" name="name" value={this.state.name} className="form-control" onChange={this.handleName}/>
                    </div>
                    <div className="form-group">
                        <label>Phone</label>
                        <input type="text" name="phone" value={this.state.phone} className="form-control" onChange={this.handlePhone}/>
                    </div>
                    <div className="form-group">
                        <label>Persons</label>
                        <select type="text" name="person" value={this.state.person} className="form-control" onChange={this.handlePerson}>
                           <option value="1">one</option>
                           <option value="2">Two</option>
                           <option value="3">Three</option>
                        </select>
                    </div>
                    <div className="form-group">
                        <label>Address</label>
                        <input type="text" name="address" value={this.state.address} className="form-control" onChange={this.handleAddress}/>
                    </div>
                    <Link to={`/details/${this.props.match.params.id}`} className="btn btn-danger">Back</Link>&nbsp;
                    <button className="btn btn-success" onClick={this.handleSubmit}>Submit</button>
                </div>
            </div>
        )
    }
}
export default Placeorder;