import React,{Component} from 'react';
import axios from 'axios';

let sorturl="https://liveapis.herokuapp.com/restaurant";

class sortFilter extends Component{
    sortfilter=(event)=>{
        let sortType=event.target.value;
        let mealtype=sessionStorage.getItem('tripid');
        let outurl
        outurl=`${sorturl}/${mealtype}?sort=${sortType}`
        axios.get(outurl)
        .then((response)=>{this.props.sortdata(response.data)})
    }
    render(){
        return(
            <React.Fragment>
                <center>Sort Filter</center>
                <div onChange={this.sortfilter}>
                    <label className="radio">
                        <input type="radio" value="1" name="sort"/>Low-High
                    </label>
                    <label className="radio">
                        <input type="radio" value="-1" name="sort"/>High-Low
                    </label>
                </div>
            </React.Fragment>
        )
    }
}
export default sortFilter;